/* @import '../../../ui-kit/styles/global.css'; */

html, body, #root {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.rtc-video {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
}

.rtc-screen-share {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
  background: black;
}

.rtc-screen-share video{
  object-fit: contain !important;
}


.edu-room .layout {
  position: relative;
}

.layout-aside-normal {
  max-width: 300px;
  min-width: 300px;
  flex: 1;
  gap: 2px;
  padding-left: 2px;
  position: relative;
}

.layout-aside.layout-aside-fullscreen {
  position: absolute;
  bottom: 0;
  right: 0;
  border: none;
}

.layout-aside-fullscreen {
  height: 50%;
}

.layout-aside-fullscreen .chat-header {
  border-top: 1px solid #ececf1;
}

.layout-aside-normal .icon-close {
  display: none;
}

.screen-share-player-container {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  align-items: center;
  flex-direction: column;
  z-index: 2;
}

.agora-board-resources .tabs {
  position: inherit;
  top: 0;
  left: 0;
}

.agora-board-resources input[type=file] {
  display: none;
}

.mirror canvas{
  transform: scaleX(1) !important;
}

.mirror video{
  transform: scaleX(1) !important;
}

#netless {
  z-index: 0;
}

.filename {
  @apply text-white font-medium;
  font-size: 12px;
  margin-left: 5px;
  /* text-shadow: 0 1px 2px rgb(13 29 61 / 80%); */
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 71px;
}
.video-marquee-tab {
  flex: 1;
}

.video-marquee-tab .tabs-content{
  height: 100%;
}

.video-marquee-tab  .tabs-nav-list{
  width: 100%;
  flex-direction: row;
  justify-content: space-around;
}

.video-marquee-tab .tabs-ink-bar{
  display: none;
}

.video-marquee-tab .tabs-nav-operations {
  display: none;
}

.video-marquee-tab .tabs-tab + .tabs-tab {
  margin: 0;
}

.video-marquee-tab > .tabs-nav::before,
.video-marquee-tab > div > .tabs-nav::before{
  border: none;
}
.tabs-tab .video-marquee-tab-btn {
  width: 8.59vw !important;
  height: 44px  !important;
  min-width: 110px;
  border-radius: 5px  !important;
  background: #D8D8D8  !important;
  font-size: 17px;
  font-weight: 400;
  color: #2D353A;
}
#app > div.layout.layout-col.edu-room > div > aside > div.tabs.tabs-top.video-marquee-tab > div.tabs-nav > div.tabs-nav-operations {
  display: none;
}

.video-marquee-tab-btn > img {
  width: 15px;
  position: absolute;
  right: 25px;
  top: 50%;
  transform: translateY(-50%);
}

.tabs-tab-active .video-marquee-tab-btn {
  background: #38AE48  !important;
  color: #FFFFFF;
}

.video-marquee-tab > .tabs-nav-wrap {
  width: 100% !important;
}


.video-marquee-pin ~ .board-box {
  margin-top: 165px;
}

.video-marquee-pin-empty {
  margin-top: 108px;
  @apply flex flex-col items-center;
}
.video-marquee-pin-empty > img {
  width: 191px;
  height: 147px;
}

.video-marquee-pin-empty > span {
  font-size: 14px;
  font-weight: 400;
  color: #888888;
  margin-top: 14px;
}
.board-box {
  position: relative;
  height: 100%;
}

.column {
  display: flex;
  flex-direction: column;
}

.fullscreen .layout-aside {
  display: none;
}

/* .fullscreen .video-player {
  display: none;
} */

.small-class-chat {
  position: absolute;
  bottom: 8px;
  right: 11px;
}

.fullscreen .video-marquee-pin ~ .board-box {
  margin-top: 0px;
}

.placeholder-desc {
  position: absolute;
  font-size: 13px;
  font-weight: 400;
  color: #7d8798;
  left: 0;
  right: 0;
  bottom: 15px;
  margin: auto;
  text-align: center;
  z-index: 999;
}