.placeholder {
  @apply w-full h-full flex flex-col items-center justify-center pt-36 pb-36;
}

.placeholder-desc {
  margin-top: 46px;
  font-size: 13px;
  font-weight: 400;
  color: #7d8798;
}

.camera-placeholder {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
}

.teacher-video-player.camera-placeholder.camera-loading-placeholder::before{
  content: '等待老师进入教室...';
  background-image: none;
  width: 137px;
  height: 21px;
  font-size: 15px;
  font-weight: 600;
  color: #454545;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.camera-placeholder.camera-loading-placeholder, .camera-placeholder.camera-broken-placeholder::before, .camera-placeholder.camera-muted-placeholder {
  background-color: #E8E8E8;
}

.camera-placeholder.camera-loading-placeholder::before, .camera-placeholder.camera-broken-placeholder::before, .camera-placeholder.camera-muted-placeholder::before{
  content: ' ';
  background-image: url('./assets/no-body.png');
  background-position: center;
  background-size: 100%;
  border-radius: 4px;
  width: 123px;
  height: 140px;
  display: flex;
  background-repeat: no-repeat;
  background-size: 100%;
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
}

.camera-placeholder.camera-broken-placeholder::before {
  /* background-image: url('./assets/camera-broken.png'); */
}

